import {api} from "/ts/util/api";
import opg from "/ts/opg";
import Table from "/ts/ui/Table";
import PopUp from "../../../ts/ui/Popup";
import {format} from "../../../ts/util/utils";

api({
    'ManualAddExhbitionSchedule!post': 'schedule/ManualAddExhbitionSchedule'
});

export default class ManualExhibitionSchedule {
    constructor(lng) {
        opg.wrapPanel('#formExhibitionSchedule', {
            title: ``,
            btnSearchText: `${lng.calc}`,
            btnSearchClick: () => {
                let param = $('#formExhibitionSchedule').fieldsToJson();
                if (param.barcode && param.name && param.buyerRange) {
                    api.ManualAddExhbitionSchedule({
                        barcode: param.barcode,
                        addCount: param.name,
                        buyerRange: param.buyerRange,
                        isRandom: $('#exRandom').prop('checked')
                    }, () => {
                        opg.ok('ok');
                        $('#formExhibitionSchedule').jsonToFields({
                            barcode: '',
                            name: '',
                            buyerRange: ''
                        })
                    })
                }
            }
        });
    }
}